import React, { useEffect, useState } from "react";
// import repos from "../fixtures/repos"
import Repo from "./snippets/repo"
// import fetchRepos from "../libs/fetchRepos";

export default () => {
    const [repos,setRepos] = useState([]);

    useEffect(() => {
        const data = sessionStorage.getItem("repos");
        let myRepos;
    
        if (data) {
            myRepos = JSON.parse(data);
            return setRepos(myRepos);
        }

        async function fetchRepos() {
            const response = await fetch('https://api.github.com/users/gamorales/repos');
            myRepos = await response.json();

            // Caching ajax request to avoid multiple requests to github
            sessionStorage.setItem("repos", JSON.stringify(myRepos));

            setRepos(myRepos);
        }

        fetchRepos();
    }, [])

    return (
        <div className="max-w-4xl mx-auto mt-4">
            <header className="text-center">
                <h2 className="text-3xl font-bold">Github</h2>
                <p>Mis repositorios públicos</p>
            </header>
            <ul className="repos-list mt-2">
                {
                    repos.map((repo) => {
                        return <Repo repo={repo} key={repo.id} />
                    })
                }
            </ul>
        </div>
    )
}