import React from "react";
import { useStaticQuery, graphql } from "gatsby";

export default () => {
    const data = useStaticQuery(graphql`{
        githubJson {
            repos {
                id
                name
                html_url
                description
            }
        }
    }`);

    return (
        <section>
            <div className="mt-8">
                <div className="max-w-4xl mx-auto">
                    <h2 className="text-3xl font-bold text-center">Mis repos</h2>
                    <div className="flex mt-2">
                        {
                            data.githubJson.repos.map(repo => (
                                <div className="shadow p-8 bg-white mr-4 rounded" key={repo.id}>
                                    <h4 className="font-bold">{repo.name} - {repo.id}</h4>
                                    <div>
                                        <span className="inline-block bg-gray-400 text-gray-800 p-2 mt-2 radius">
                                            <a href={repo.html_url} target="_blank" rel="noopener noreferrer">Repositorio</a>
                                        </span>
                                        <p>{repo.description}</p>
                                    </div>
                                </div>
                            ))
                        }
                    </div>
                </div>
            </div>
        </section>
    );
}